<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>后台账号管理</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
           <el-table-column
          align='center'
          prop="account"
          label="账号">
          </el-table-column>
          <el-table-column
          align='center'
          prop="name"
          label="用户名">
          </el-table-column>
          <el-table-column
          label="登录时间"
          prop="create_time"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
            <template slot-scope='scope'>
              <span
              @click="changeState(scope.$index, scope.row.id, tableData, scope.row.state)"
              :class="scope.row.state == '2'?'tag_success':'tag_danger'">
              {{scope.row.state == "2"?'启用':'禁用'}}
              </span>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="180"
          label="操作">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editorlist(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.id)"><span>删除</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <el-dialog
        :title="addTableData.aid==='-1'?'新增后台用户':'修改后台用户'" v-model="addTableData.id"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :visible.sync="dialogVisible"
        width="30%">
        <el-form :model="addTableData" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="账号">
            <el-input v-model="addTableData.account" placeholder="请输入登录账号"></el-input>
          </el-form-item>
          <el-form-item label="用户名">
            <el-input v-model="addTableData.name" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="密码">
            <el-input v-model="addTableData.password" placeholder="请输入密码" type="password"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'list',
  data () {
    return {
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      loading: null,
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      tableData: [],
      total: 0,
      sureLoading: false,
      aid: '',
      dialogVisible: false,
      dialogTableVisible: false,
      addTableData: {
        account: '',
        name: '',
        password: '',
        aid: ''
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'user',
            op: '',
            condition: {
              id: ''
            },
            data: {
              account: this.addTableData.account,
              name: this.addTableData.name,
              password: this.addTableData.password
            }
          }
          if (_this.addTableData.aid === '-1') {
            da.op = 'user_create'
          } else {
            da.op = 'user_edit'
            da.condition.id = this.addTableData.id
          }
          this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisible = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    add (formName) {
      this.dialogVisible = true
      this.addTableData = {
        account: '',
        name: '',
        password: '',
        aid: '-1'
      }
      this.$refs[formName].resetFields()
    },
    editorlist (index, id, list, formName) {
      this.dialogVisible = true
      this.addTableData = Object.assign({}, list[index])
      this.sureLoading = false
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'user',
        op: 'user_list'
      }
      this.$axios.post(domain.testUrl + '/admin/users/rulegroup?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 删除
    deleted (id) {
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'user',
          op: 'user_edit',
          condition: {
            id: id
          },
          data: {
            account: this.addTableData.account,
            name: this.addTableData.name,
            password: this.addTableData.password,
            state: '0'
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({type: 'success', message: '删除成功!'})
          } else {
            _this.$notify({title: '提示', message: da.data.message, type: 'warning'})
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      })
    },
    changeState (index, id, list, state) {
      this.addTableData = Object.assign({}, list[index])
      let _this = this
      if (state === 1) {
        state = 2
      } else {
        state = 1
      }
      let d = {
        tag: 'user',
        op: 'user_edit',
        condition: {
          id: id
        },
        data: {
          account: this.addTableData.account,
          name: this.addTableData.name,
          password: this.addTableData.password,
          state: state
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



