              <p class="store_order-data bluecolor">新增会员</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">今日新增用户</span>
                <span class="fr store-right">{{membertoday}}</span>
              </p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">本月新增用户</span>
                <span class="fr store-right">{{membermoth}}</span>
              </p>
            </li>
            <li class="store_order orange_tint">
              <p class="store_order-data orange_deep">活跃人数</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
