<script>
import qs from 'qs'
import domain from '../../static/js/url.js'
export default {
  name: 'chart',
  data () {
    return {
      authour: 'WiseWrong',
      activeName: '1',
      // temp:true,
      Alist: [{
        name: '正在服务技师'
      }, {
        name: '等待服务技师'
      }],
      active: '正在服务技师',
      membertoday: '',
      membermoth: '',
      activemoth: '',
      activetoday: '',
      ordermoth: '',
      ordertoday: ''
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    selected (name) {
      this.active = name
    },
    getList () {
      let _this = this
      this.$axios.post(domain.testUrl + '/admin/index/membercount', qs.stringify(), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.membertoday = da.data.todayjoin.today
          this.membermoth = da.data.todayjoin.themonth
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
      this.$axios.post(domain.testUrl + '/admin/index/activepeople', qs.stringify(), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.activemoth = da.data.today.length
          this.activetoday = da.data.month.length
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
      this.$axios.post(domain.testUrl + '/admin/index/ordercounter', qs.stringify(), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.ordermoth = da.data.todayjoin.themonth
          this.ordertoday = da.data.todayjoin.today
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
    }
  }
