<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>广告列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
        <div class="from-group">
          <el-button type="primary" @click="add">新增</el-button>
        </div>
      </form>
    </div>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            label="图片">
            <template slot-scope="scope">
              <img v-if="!!scope.row.img" :src="scope.row.img" width="150" height="100">
              <template v-else>无</template>
            </template>
          </el-table-column>
          <el-table-column
            prop="title"
            label="标题"
            width="180">
          </el-table-column>
          <el-table-column
            prop="url"
            label="跳转地址"
            width="180">
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
          <template slot-scope='scope'>
              <span @click="changeState(scope.$index,tableData,scope.row.id,scope.row.state)"
              :class="scope.row.state == '1'?'tag_success':'tag_danger'">{{scope.row.state == "1"?'启用':'禁用'}}
              </span>
            </template>
          </el-table-column>
          <el-table-column
            align='center'
            label="操作">
            <template slot-scope="scope">
              <button type="button" class="el-button el-button--default el-button--small" @click="editor(scope.$index,scope.row.id,tableData)"><span>编辑</span></button>
              <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.$index,tableData,scope.row.id)">
                <span>删除</span>
              </button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
        <!-- 新增 -->
        <!-- :close-on-click-modal="false"
        :close-on-press-escape="false" -->
        <el-dialog
        :title="addTableData.aid==='-1'?'新增广告':'修改广告'" v-model="addTableData.id"
        width='500px'
        :visible.sync="addManagerVisible">
          <el-form :model="addTableData" label-width="100px" class="demo-ruleForm">
            <el-form-item label="标题">
              <el-input v-model="addTableData.title" placeholder="请输入标题"></el-input>
            </el-form-item>
            <el-form-item label="跳转地址">
              <el-input v-model="addTableData.url" placeholder="请输入跳转地址"></el-input>
            </el-form-item>
          <el-form-item label="封面图片">
              <el-upload
                id="el-upload1"
                class="avatar-uploader avatar-uploader_356x178 avatar-uploader_contain"
                action="11111"
                :show-file-list="false"
                :on-change='formImageChange'
                :auto-upload='false'
                :on-success="handleAvatarSuccess">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model='addTableData.state' style='width: 50%;'>
                <el-option
                  :label="'启用'"
                  :value='1'></el-option>
                <el-option
                  :label="'禁用'"
                  :value='2'></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addManagerVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'>提 交</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      selectData: [],
      total: 0,
      tableData: [],
      addManagerVisible: false,
      state: 1,
      sureLoading: false,
      imageUrl: '',
      imageUrld: '',
      addTableData: {
        url: '',
        state: 1,
        imageUrl: '',
        aid: ''
      },
      forid: '',
      restaurants: []
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    add () {
      this.imageUrl = ''
      this.addTableData = {
        url: '',
        state: 1,
        imageUrl: '',
        aid: '-1'
      }
      this.addManagerVisible = true
    },
    formImageChange (file, fileList) {
      this.forid = 1
      this.imageUrl = URL.createObjectURL(file.raw)
      let d = new FormData()
      d.append('image', file.raw)
      d.append('dic', 'other')
      this.$axios.post(domain.testUrl + '/admin/upload/index', d, {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.addTableData.image = da.data
          this.$Notice.success({title: da.message})
        } else {
          this.loading = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    handleSizeChange (size) {
      this.pagesize = size
      this.getList()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    editor (index, id, list) {
      this.addManagerVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
      this.addTableData.image = list[index].img
    },
    getList () {
      let d = {
        tag: 'ad_config',
        op: 'get'
      }
      var pa = this.currentPage
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    submitForm () {
      let _this = this
      this.sureLoading = true
      if (_this.addTableData.aid === '-1') {
        let d = new FormData()
        d.append('tag', 'ad_config')
        d.append('op', 'add')
        d.append('data[url]', _this.addTableData.url)
        d.append('data[title]', _this.addTableData.title)
        d.append('data[state]', _this.addTableData.state)
        d.append('data[img]', _this.addTableData.image)
        this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', d, {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            this.getList()
            this.sureLoading = false
            this.addManagerVisible = false
            _this.$Notice.success({title: da.message})
          } else {
            this.sureLoading = false
            _this.$Notice.warning({title: da.message})
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      } else {
        let d = new FormData()
        d.append('tag', 'ad_config')
        d.append('op', 'update')
        d.append('data[url]', _this.addTableData.url)
        d.append('data[title]', _this.addTableData.title)
        d.append('data[state]', _this.addTableData.state)
        d.append('data[id]', _this.addTableData.id)
        d.append('data[img]', _this.addTableData.image)
        this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', d, {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            this.getList()
            this.sureLoading = false
            this.addManagerVisible = false
            _this.$Notice.success({title: da.message})
          } else {
            this.sureLoading = false
            _this.$Notice.warning({title: da.message})
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      }
    },
    // 删除
    deleted (index, list, id) {
      this.addTableData = Object.assign({}, list[index])
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'ad_config',
          op: 'state',
          data: {
            id: id,
            state: 0
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
          } else {
            _this.$notify({
              title: '提示',
              message: da.data.message,
              type: 'warning'
            })
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      })
    },
    changeState (index, list, id, state) {
      this.addTableData = Object.assign({}, list[index])
      let _this = this
      if (state === 1) {
        state = 2
      } else {
        state = 1
      }
      let d = {
        tag: 'ad_config',
        op: 'state',
        data: {
          id: id,
          state: state
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>



