<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>系统基本设置</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="set_content">
          <el-form :model="addTableData" label-width="200px" class="demo-ruleForm">
            <el-form-item label="签到获取积分数量：">
              <el-input v-model="addTableData.title" placeholder="请输入签到获取积分数量"></el-input>
            </el-form-item>
            <el-form-item label="邀请好友获取积分数量：">
              <el-input v-model="addTableData.url" placeholder="请输入邀请好友获取积分数量"></el-input>
            </el-form-item>
            <el-form-item label="客服电话：">
              <el-input v-model="addTableData.url" placeholder="请输入客服电话"></el-input>
            </el-form-item>
          </el-form>
        <div slot="footer" class="dialog-footer tc">
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'>提 交</el-button>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      selectData: [],
      tableData: [],
      addManagerVisible: false,
      imageUrl: '',
      state: 1,
      sureLoading: false,
      addTableData: {
        url: '',
        state: 1,
        imageUrl: 'adsfjklasfjklsajfk',
        aid: ''
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    add () {
      this.imageUrl = ''
      this.addTableData = {
        url: '',
        state: 1,
        imageUrl: 'adsfjklasfjklsajfk',
        aid: '-1'
      }
      this.addManagerVisible = true
    },
    getList () {
      let d = {
        tag: 'banner_config',
        op: 'get'
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    submitForm () {
      let _this = this
      this.sureLoading = true
      let da = {
        tag: 'banner_config',
        op: 'add',
        data: {
          img: 'sss.img',
          url: this.addTableData.url,
          title: this.addTableData.title,
          state: this.addTableData.state
        }
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.getList()
          this.sureLoading = false
          this.addManagerVisible = false
          _this.$Notice.success({title: da.message})
        } else {
          this.sureLoading = false
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
</style>



