<template>
  <header>
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      background-color="#3B3B41"
      @select="handleSelect"
      text-color="black"
      active-text-color="#5cacfe">
      <div>
        <div class="fl">
          <i class="name_logo fl"></i>
          <p class="name_title fr">爱医慧医药管理系统</p>
        </div>
        <ul class="white flex">
          <li>
            <i class="icc icc_admin"></i>
            <span class="icc_span">admin</span>
          </li>
          <li>
            <i class="icc icc_out"></i>
            <span class="icc_span">退出</span>
          </li>
        </ul>
      </div>
    </el-menu>
  </header>
</template>

<script>
export default {
  name: 'headerComponent',
  data () {
    return {
    }
  },
  methods: {
  }
}
</script>

<style>
.el-submenu__title{
    color: #fff !important;
}
  .el-menu--horizontal>.el-submenu {
    float: right!important;
}
  .hahaha .el-menu-item.is-active {
    color: #fff !important;
  }
  .el-menu--horizontal .el-icon-arrow-down {
    color: #fff;
  }
  .flex{
    display: flex;
    justify-content: flex-end;
  }
  .flex li{
     width: 100px;
     text-align: center;
     height: 61px;
     line-height: 55px;
  }
  .flex li:hover{
    opacity: 0.4;
    cursor: pointer;
  }

</style>



