
<template>
  <div id="log">
    <div class="login_m">
        <div class="login_logo">爱医慧医药后台管理系统</div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm login_boder" @keyup.enter.native="submitForm('ruleForm')">
            <el-form-item label="用户名" prop="userphone" class="el-login_name w_per-90">
                <el-input v-model.number="ruleForm.userphone"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password" class="w_per-90">
                <el-input type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
            </el-form-item>
            <div class="el-login_sub">
                <el-button class="login_sub" type="primary" @click="submitForm('ruleForm')" :loading="btnLoading">登录<span v-if="btnLoading">中...</span></el-button>
            </div>
        </el-form>
    </div>
  </div>
</template>
<script>
import qs from 'qs'
import domain from '../../static/js/url.js'
export default {
  name: `login`,
  data () {
    const validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        if (this.ruleForm.password !== '') {
          this.$refs.ruleForm.validateField('password')
        }
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        userphone: 'admin',
        password: '123456'
      },
      rules: {
        userphone: [{ validator: validateUsername, trigger: 'blur' }],
        password: [{ validator: validatePassword, trigger: 'blur' }]
      },
      btnLoading: false
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let d = {
            tag: 'user',
            op: 'user_login',
            data: {
              account: '',
              password: ''
            }
          }
          d.data.account = this.ruleForm.userphone
          d.data.password = this.ruleForm.password
          this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(d), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.$router.push('dashboard')
            } else {
              this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style>
#log{
  height: 100%;
  background: url(../../static/img/login_bgx.gif)!important;
}
</style>



