<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>药品列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative" :model="formInline">
            <div class="from-group">
              <el-input
                v-model="formInline.name"
                placeholder="请输入药品名"
                clearable>
              </el-input>
            </div>
            <div class="from-group from_number">
              <el-input
                v-model="formInline.sn"
                placeholder="请输入批次号"
                clearable>
              </el-input>
            </div>
            <div class="from-group">
              <el-date-picker
              v-model="formInline.enddate"
              default-time="00:00:00"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="选择到期日期">
            </el-date-picker>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="getList(true)">查询</el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            class="picture_drug"
            label="图片">
            <template slot-scope="scope">
              <img v-if="!!scope.row.imgurl" :src="scope.row.imgurl" width="80" height="50">
              <template v-else>无</template>
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          prop="company"
          label="厂家">
          </el-table-column>
          <el-table-column
          label="药品名"
          prop="name"
          width="180"
          align='center'>
          </el-table-column>
          <el-table-column
          width="150"
          label="批次号"
          align='center'>
          <template slot-scope="scope">
            <p class="dtail_number" @click='detail(scope.row.id)'>{{scope.row.sn}}</p>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          prop="price"
          label="原价">
          </el-table-column>
          <el-table-column
          align='center'
          prop="trueprice"
          label="折后价">
          </el-table-column>
          <el-table-column
          align='center'
          prop="specifications"
          label="规格">
          </el-table-column>
          <el-table-column
          align='center'
          prop="enddate"
          label="到期时间">
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
              <template slot-scope='scope'>
              <span
              @click="changeState(scope.row.id,scope.row.status)"
              :class="scope.row.status == '1'?'tag_success':'tag_danger'"
              >{{scope.row.status == "1"?'上架':'下架'}}
              </span>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="180"
          label="操作">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editorlist(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.id)"><span>删除</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <!-- :close-on-click-modal="false"
        :close-on-press-escape="false" -->
        <el-dialog
        :title="addTableData.aid==='-1'?'新增药品':'修改药品'" v-model="addTableData.id"
        :visible.sync="dialogVisible"
        width="35%">
        <el-form :model="addTableData" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="药品名称" prop="name">
            <el-input v-model="addTableData.name" placeholder="请输入药品名称"></el-input>
          </el-form-item>
          <el-form-item label="药品图片">
            <el-upload
              id="el-upload1"
              class="avatar-uploader avatar-uploader_356x178 avatar-uploader_contain"
              action="11111"
              :show-file-list="false"
              :on-change='formImageChange'
              :auto-upload='false'
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="公司名称" prop="company">
            <el-input v-model="addTableData.company" placeholder="请输入所在公司名"></el-input>
          </el-form-item>
          <el-form-item label="规格" prop="specifications">
            <el-input v-model="addTableData.specifications" placeholder="请输入药品规格"></el-input>
          </el-form-item>
          <el-form-item label="原价/元" prop="price">
            <el-input v-model="addTableData.price" placeholder="请输入药品价格"></el-input>
          </el-form-item>
           <el-form-item label="折后价/元">
            <el-input v-model="addTableData.trueprice" placeholder="请输入药品价格"></el-input>
          </el-form-item>
          <el-form-item label="批次号" prop="sn">
            <el-input v-model="addTableData.sn" placeholder="请输入药品批次号"></el-input>
          </el-form-item>
          <el-form-item label="设为推荐药品">
            <el-checkbox v-model="addTableData.tag">推荐药品</el-checkbox>
          </el-form-item>
          <el-form-item label="保质期/月" prop="period">
            <el-input v-model="addTableData.period" placeholder="请输入药品保质期"></el-input>
          </el-form-item>
          <el-form-item label="到期日期" prop="enddate">
            <el-date-picker
              v-model="addTableData.enddate"
              type="date"
              placeholder="请选择药品到期日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="商品状态">
            <el-select v-model='addTableData.sta_goods' style='width: 50%;'>
              <el-option
                label='普通'
                :value='1'>普通</el-option>
              <el-option
                label='折扣'
                :value='2'>折扣</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="状态">
          <el-select v-model='addTableData.status' style='width: 50%;'>
            <el-option
              :label="'上架'"
              :value='1'></el-option>
            <el-option
              :label="'下架'"
              :value='2'></el-option>
          </el-select>
        </el-form-item>
          <div class="detail">详情</div>
          <UE v-if='dialogVisible' class="ueeclsjdf" :defaultMsg='defaultMsg' :config='config' ref="ue"></UE>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
import UE from '../../components/ue/ue.vue'
export default {
  name: 'list',
  components: {UE},
  data () {
    return {
      editor: '',
      defaultMsg: '',
      defaultMsgName: '',
      abb: '',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      imageUrl: '',
      isshow: true,
      loading: null,
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      tableData: [],
      total: 0,
      sureLoading: false,
      aid: '',
      // 搜索
      formInline: {
        name: '',
        sn: '',
        enddate: ''
      },
      dialogVisible: false,
      dialogTableVisible: false,
      addTableData: {
        name: '',
        aid: '',
        imgurl: '',
        imageUrl: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        tag: false
      },
      rules: {
        name: [{ required: true, message: '请输入药品名称', trigger: 'blur' }],
        company: [{ required: true, message: '请输入公司', trigger: 'blur' }],
        specifications: [{ required: true, message: '请输入规格', trigger: 'blur' }],
        price: [{ required: true, message: '请输入价格', trigger: 'blur' }],
        sn: [{ required: true, message: '请输入批次号', trigger: 'blur' }],
        period: [{ required: true, message: '请输入药品保质期', trigger: 'blur' }],
        enddate: [{ required: true, message: '请选择药品到期日期', trigger: 'blur' }]
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    formImageChange (file, fileList) {
      this.abb = 1
      this.imageUrl = URL.createObjectURL(file.raw)
      let d = new FormData()
      d.append('image', file.raw)
      d.append('dic', 'product')
      this.$axios.post(domain.testUrl + '/admin/upload/index', d, {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.addTableData.imgurl = da.data
          this.$Notice.success({title: da.message})
        } else {
          this.loading = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 < 100
      if (!isJPG) {
        this.$Notice.error({title: '上传头像图片只能是 JPG 格式!'})
      }
      if (!isLt2M) {
        this.$Notice.error({title: '上传头像图片大小不符'})
      }
      return isJPG && isLt2M
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          if (this.addTableData.tag === true) {
            this.addTableData.tag = 1
          } else {
            this.addTableData.tag = 0
          }
          if (_this.addTableData.aid === '-1') { 
            let da = {
              tag: 'product',
              op: 'add',
              data: {
                name: this.addTableData.name,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                trueprice: '',
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                imgurl: this.addTableData.imgurl,
                remarks: '',
                detail: this.$refs.ue.getUEContent(),
                tag: this.addTableData.tag
              }
            }
            if(!this.addTableData.trueprice){
              da.data.trueprice = this.addTableData.price
            }else{
              da.data.trueprice = this.addTableData.trueprice
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
                _this.$Notice.success({title: da.message})
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: '请求失败，请重试'})
            })
          } else {
            let da = {
              tag: 'product',
              op: 'update',
              condition: {
                id: this.addTableData.id
              },
              data: {
                name: this.addTableData.name,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                trueprice: this.addTableData.trueprice,
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                imgurl: this.addTableData.imgurl,
                tag: this.addTableData.tag,
                remarks: '',
                detail: this.$refs.ue.getUEContent()
              }
            }
             if(!this.addTableData.trueprice){
              da.data.trueprice = this.addTableData.price
            }else{
              da.data.trueprice = this.addTableData.trueprice
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: '请求失败，请重试'})
            })
          }
        } else {
          return false
        }
      })
    },
    add (formName) {
      this.dialogVisible = true
      this.imageUrl = ''
      this.addTableData = {
        name: '',
        aid: '-1',
        imageUrl: '',
        imgurl: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        tag: false
      }
      this.$refs[formName].resetFields()
      this.defaultMsg = ''
      this.abb = ''
    },
    editorlist (index, id, list, formName) {
      this.abb = ''
      this.dialogVisible = true
      this.addTableData = Object.assign({}, list[index])
      this.imageUrl = list[index].imgurl
      this.defaultMsg = list[index].detail || ''
      this.sureLoading = false
      this.$refs[formName].resetFields()
      if (list[index].tag === 1) {
        this.addTableData.tag = true
      } else {
        this.addTableData.tag = false
      }
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline.name,
          'sn': this.formInline.sn,
          'enddate': this.formInline.enddate
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          // this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 详情
    detail () {
      this.dialogTableVisible = true
    },
    // 删除
    deleted (id) {
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'product',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0'
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
          } else {
            _this.$notify({
              title: '提示',
              message: da.data.message,
              type: 'warning'
            })
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'product',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



