import Vue from 'vue'
import Router from 'vue-router'
// 登录
import loginComponents from '@/components/login'
import main from '@/components/main'
// 首页
import dashboardComponent from '@/components/dashboard'
import accountComponent from '@/components/account/accountlist'
// 广告设置
import bannerComponent from '@/components/banner/bannerlist'
import advertisementComponent from '@/components/banner/advertisement'
// 药品
import drugsComponent from '@/components/drugs/drugslist'
// 订单
import orderComponent from '@/components/order/orderlist'
// 消息
import newsComponent from '@/components/news/newslist'
import drugshareComponent from '@/components/news/drugshare'
import sendlistComponent from '@/components/news/sendlist'
// 系统设置
import ruleComponent from '@/components/system/rule'
import shareComponent from '@/components/system/share'
import setupComponent from '@/components/system/setup'
import messageComponent from '@/components/system/message'
import integralComponent from '@/components/system/integral'
import wechatComponent from '@/components/system/wechat'
import drawComponent from '@/components/system/draw'
// 权限管理
import backaccountComponent from '@/components/jurisdiction/backaccount'
import powerComponent from '@/components/jurisdiction/power'
import logComponent from '@/components/jurisdiction/log'
import roleComponent from '@/components/jurisdiction/role'
// 数据分析
import analysisComponent from '@/components/analysis/analysislist'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: loginComponents
    },
    {
      path: '/main',
      name: 'main',
      component: main
    },
    {
      path: '/dashboard',
      name: 'dashboard',
      component: main,
      children: [{
        path: '/dashboard',
        name: 'dashboard',
        component: dashboardComponent
      }]
    }, {
      path: '/account',
      component: main,
      children: [{
        path: '/account/accountlist',
        name: 'accountlist',
        component: accountComponent
      }]
    },
    {
      path: '/banner',
      component: main,
      children: [{
        path: '/banner/bannerlist',
        name: 'bannerlist',
        component: bannerComponent
      }, {
        path: '/banner/advertisement',
        name: 'advertisement',
        component: advertisementComponent
      }]
    }, {
      path: '/drugs',
      component: main,
      children: [{
        path: '/drugs/drugslist',
        name: 'drugslist',
        component: drugsComponent
      }]
    }, {
      path: '/order',
      component: main,
      children: [{
        path: '/order/orderlist',
        name: 'orderlist',
        component: orderComponent
      }]
    }, {
      path: '/news',
      component: main,
      children: [{
        path: '/news/newslist',
        name: 'newslist',
        component: newsComponent
      }, {
        path: '/news/drugshare',
        name: 'drugshare',
        component: drugshareComponent
      }, {
        path: '/news/sendlist',
        name: 'sendlist',
        component: sendlistComponent
      }]
    }, {
      path: '/system',
      component: main,
      children: [{
        path: '/system/rule',
        name: 'rule',
        component: ruleComponent
      }, {
        path: '/system/share',
        name: 'share',
        component: shareComponent
      }, {
        path: '/system/setup',
        name: 'setup',
        component: setupComponent
      }, {
        path: '/system/message',
        name: 'message',
        component: messageComponent
      }, {
        path: '/system/integral',
        name: 'integral',
        component: integralComponent
      }, {
        path: '/system/wechat',
        name: 'wechat',
        component: wechatComponent
      }, {
        path: '/system/draw',
        name: 'draw',
        component: drawComponent
      }]
    }, {
      path: '/jurisdiction',
      component: main,
      children: [{
        path: '/jurisdiction/backaccount',
        name: 'backaccount',
        component: backaccountComponent
      }, {
        path: '/jurisdiction/power',
        name: 'power',
        component: powerComponent
      }, {
        path: '/jurisdiction/log',
        name: 'log',
        component: logComponent
      }, {
        path: '/jurisdiction/role',
        name: 'role',
        component: roleComponent
      }]
    }, {
      path: '/analysis',
      component: main,
      children: [{
        path: '/analysis/analysislist',
        name: 'analysislist',
        component: analysisComponent
      }]
    }
  ]
})



