<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>后台权限管理</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增权限</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
           <el-table-column
          align='center'
          prop="name"
          label="权限名称">
          </el-table-column>
          <el-table-column
          align='center'
          prop="title"
          label="权限描述">
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
              <template slot-scope='scope'>
              <span
              @click="changeState(scope.row.id,scope.row.status)"
              :class="scope.row.status == '1'?'tag_success':'tag_danger'"
              >{{scope.row.status == "1"?'启用':'禁用'}}
              </span>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="180"
          label="操作">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editorlist(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <el-dialog
        :title="addTableData.aid==='-1'?'新增后台权限':'修改后台权限'" v-model="addTableData.id"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :visible.sync="dialogVisible"
        width="30%">
        <el-form :model="addTableData" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="权限名称/路由">
            <el-input v-model="addTableData.name" placeholder="请输入权限名称/路由"></el-input>
          </el-form-item>
          <el-form-item label="权限描述">
            <el-input v-model="addTableData.title" placeholder="请输入权限描述"></el-input>
          </el-form-item>
          <el-form-item label="状态">
          <el-select v-model='addTableData.status' style='width: 50%;'>
            <el-option
              :label="'启用'"
              :value='1'></el-option>
            <el-option
              :label="'禁用'"
              :value='2'></el-option>
          </el-select>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'list',
  data () {
    return {
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      loading: null,
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      tableData: [],
      total: 0,
      sureLoading: false,
      aid: '',
      dialogVisible: false,
      dialogTableVisible: false,
      addTableData: {
        name: '',
        aid: '',
        status: 1,
        id: ''
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          let da = {
            tag: 'auth_rule',
            op: '',
            data: {
              name: this.addTableData.name,
              title: this.addTableData.title,
              status: this.addTableData.status,
              id: ''
            }
          }
          if (_this.addTableData.aid === '-1') {
            da.op = 'add'
          } else {
            da.op = 'update'
            da.data.id = this.addTableData.id
          }
          this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisible = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
        } else {
          return false
        }
      })
    },
    add (formName) {
      this.dialogVisible = true
      this.addTableData = {
        aid: '-1',
        name: '',
        title: '',
        status: 1
      }
      this.$refs[formName].resetFields()
    },
    editorlist (index, id, list, formName) {
      this.dialogVisible = true
      this.addTableData = Object.assign({}, list[index])
      this.sureLoading = false
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'auth_rule',
        op: 'get',
        ispage: '1'
      }
      this.$axios.post(domain.testUrl + '/admin/users/rulegroup?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'auth_rule',
        op: 'state',
        data: {
          id: id,
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/users/rulegroup', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({type: 'success', message: da.message})
        } else {
          _this.$notify({title: '提示', message: da.message, type: 'warning'})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



