<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>抽奖</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="prize_content">
            <form :model="addTableData">
              <div class="main_table">
                <h2 class="main-flex">转盘内容</h2>
                <el-button class="main-flex add_prize" type="primary" @click="add('ruleForm')">新增</el-button>
              </div>
              <ul>
                <li class="prize_name over" v-for="(item,k) in arry" :key="k">
                  <div class="fl prize"><label for="prize">奖品：</label><input type="text" id="prize" v-model="item.prize" placeholder="请输入奖品名称"></div>
                  <div class="fl prize"><label for="probab">中奖概率：</label><input type="text" id="probab" v-model="item.v" placeholder="请输入中奖概率"></div>
                  <Icon class="close" @click="detal" type="md-close" />
                </li>
              </ul>
              <h2 class="prize_count">每人转动次数</h2>
              <ul>
                <li class="prize_name over">
                  <div class="fl prize"><label for="prize">用户最小等级：</label><input type="text" id="prize" v-model="addTableData.minlevel" placeholder="请输入用户最小等级"></div>
                  <div class="fl prize"><label for="probab">用户最大等级：</label><input type="text" id="probab" v-model="addTableData.maxlevel" placeholder="请输入用户最大等级"></div>
                  <div class="fl prize"><label for="probab">抽奖次数：</label><input type="text" id="probab" v-model="addTableData.times" placeholder="请输入抽奖次数"></div>
                </li>
              </ul>
            </form>
        <div slot="footer" class="dialog-footer tc">
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'>提 交</el-button>
        </div>
      </div>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      selectData: [],
      tableData: [],
      addManagerVisible: false,
      imageUrl: '',
      state: 1,
      sureLoading: false,
      addTableData: {
        prize: '',
        v: '',
        minlevel: '',
        maxlevel: '',
        times: ''
      },
      arry: [{
        prize: '',
        v: ''
      }]
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    add () {
      this.addManagerVisible = true
      let cope = {
        prize: '',
        v: ''
      }
      this.arry.push(cope)
    },
    detal (val) {
      for (let index = 0; index < val.length; index++) {
        // let vv = val
        val.forEach((val, index) => {
          this.arry.forEach((v, i) => {
            if (val.rowNum === v.rowNum) {
              // i 为选中的索引
              this.arry.splice(i, 1)
            }
          })
        })
      }
    },
    getList () {
      let d = {
        tag: 'banner_config',
        op: 'get'
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    submitForm () {
      console.log(this.arry.prize)
      // let _this = this
      // this.sureLoading = true
      // let da = {
      //   data: {
      //     rules:[{
      //       prize: this.arry.prize,
      //       min: '90',
      //       max: '180',
      //       v: this.arry.v
      //     }],
      //     times: [{
      //       minlevel: '',
      //       maxlevel: '',
      //       times: ''
      //     }]
      //   }
      // }
      // this.$axios.post(domain.testUrl + '/admin/Systemsets/active_set', qs.stringify(da), {
      //   headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      // }).then(res => {
      //   let da = res.data
      //   if (da.err > 0) {
      //     this.getList()
      //     this.sureLoading = false
      //     this.addManagerVisible = false
      //     _this.$Notice.success({title: da.message})
      //   } else {
      //     this.sureLoading = false
      //     _this.$Notice.warning({title: da.message})
      //   }
      // }).catch(function () {
      //   _this.$Notice.error({title: '请求失败，请重试'})
      // })
    }
  }
}
</script>
<style>
</style>



