  methods: {
    // 代理商获取fatherid
    querySearch (queryString, cb) {
      let d = {
        tag: 'member',
        op: 'getuserbyphone',
        condition: {
          phone: this.addTableDataagent.fatherphone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.daagent = true
          this.goodshow = false
          this.agentid = da.data[0].id
          this.agentdurg()
          var restaurants = da.data.map(user => {
            let {phone, name, num1} = user
            let value = user.phone
            return {value}
          })
          var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
          cb(results)
        }
      })
    },
    createFilter (queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item) {
      let d = {
        tag: 'member',
        op: 'getuserbyphone',
        condition: {
          phone: this.addTableDataagent.fatherphone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.agentid = da.data[0].id
          this.agentdurg()
        } else {
          this.$Notice.warning({title: da.message})
        }
      })
    },
    // 代理商根据id获取出来的药品
