        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatagoods = da.data.list
          console.log(this.tableDatagoods)
          this.optionss = da.data.list
        } else {
          this.tableDatagoods = []
          this.optionss = []
          this.$Notice.warning({title: da.message})
        }
      })
    },    
