    handleCommand (id, command, type) {
      let _this = this
      this.pointid = id
      let d = {
        tag: 'member',
        op: '',
        condition: {
          id: id
        }
      }
      if (command === 'a') {
        _this.BuyaVisible = true
        d.op = 'getone'
      } else if (command === 'b') {
        _this.LevelVisible = true
        d.op = 'getchild'
      } else if (command === 'c') {
        _this.aredyVisible = true
        d.op = 'recentsearch'
      } else if (command === 'd') {
        _this.dialogVisiblepoint = true
        _this.addTableDatapoint.point = ''
      } else if (command === 'e') {
        _this.CodeVisible = true
        d.op = 'point_history'
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          if (command === 'a') {
            _this.tableDatabuy = da.data.lastOrder.info
          } else if (command === 'b') {
            _this.tableDatalevel = da.data.list
          } else if (command === 'c') {
            _this.durgData = da.data.list
            _this.totalseach = da.data.total
          } else if (command === 'e') {
            _this.codeData = da.data.list
            _this.totalpoint = da.data.total
          }
        } else {
          _this.tableDatabuy = []
          _this.tableDatalevel = []
          _this.durgData = []
          _this.codeData = []
          _this.totalpoint = 0
          _this.durgData = 0
          _this.totalseach = 0
        }
      })
    },
