    agency_level (value) {
      this.agentlevel = value
      if (this.agentlevel === -1) {//省代理
        this.loweragent = true
        this.agentre = true
        this.$axios.get('../../../static/js/area.json').then((res) => {
          var arry = res.data.map(user => {
