            this.region_district = ''
          }          
          let da = {
            tag: 'member',
            op: '',
            condition: {
              id: this.addTableDataagent.id
            },
            data: {
              type: '4',
              uname: this.addTableDataagent.uname,
              phone: this.addTableDataagent.phone,
              level: '1',
              sex: this.addTableDataagent.sex,
              address: '',
              status: this.addTableDataagent.status,
              label: '',
              agent_products: '',
              region_province: '',
              region_city: '',
              region_district: '',
              point: this.addTableDataagent.point,
              fatherid: ''
            }
          }
          if (!this.agentlevel) {//不存在
              da.data.agent_products = this.addTableDataagent.agent_products
              da.data.fatherid = this.agentid
          } else {
            if (this.agentlevel === -1) {
              da.data.agent_products = this.addTableDataagent.agent_products
              da.data.fatherid = this.agentid
            } else if (this.agentlevel === 0) {
              da.data.agent_products = this.addTableDataagent.agent_products
              da.data.fatherid = this.agentid
            } else if (this.agentlevel === 1) {
              da.data.agent_products = this.addTableDataagent.agent_products
              da.data.fatherid = this.agentid
            }
          }
          if (_this.addTableDataagent.aid === '-1') {//新增
            da.data.region_province = this.region_province
            da.data.region_city = this.region_city
            da.data.region_district = this.region_district
            da.op = 'add'
            if(!this.agentid){
              da.data.agent_products = this.arrayFilter1
              da.data.fatherid = '-2'
            }
          } else {
            this.sureLoading = false
            da.op = 'update'
            if(!this.agentid){
              da.data.agent_products = this.arrayFilter1
              da.data.fatherid = '-2'
            }
            if (this.clickagen === 1) {
              da.data.region_province = this.region_province
              da.data.region_city = this.region_city
              da.data.region_district = this.region_district
            } else {
              da.data.region_province = this.province
              da.data.region_city = this.city
              da.data.region_district = this.district
            }
            da.data.region_province = this.region_province
            da.data.region_city = this.region_city
            da.data.region_district = this.region_district
          }
          this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(da), {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }).then(res => {
            let da = res.data
            if (da.err > 0) {
              this.getList()
              this.dialogVisibleagent = false
              this.sureLoading = false
              _this.$Notice.success({title: da.message})
            } else {
              this.sureLoading = false
              _this.$Notice.warning({title: da.message})
            }
          }).catch(function () {
            _this.$Notice.error({title: '请求失败，请重试'})
          })
    },
    adddrug (formName) {
      this.dialogVisibledrug = true
      this.defaultMsg = ''
      this.areacontent = false
      this.imageUrl = ''
      this.imagelicence = ''
      this.addTableDatadrug = {
        imageUrl: '',
        licence_imgurl: '',
        shop_image: '',
        fatherphone: '',
        fathername: '',
        phone: '',
        uname: '',
        level: '',
        sex: 1,
        address: '',
        status: 1,
        shop_name: '',
        longitude: '',
        latitude: '',
        be_good_at: '',
        label: '',
        aid: '-1',
        id: '',
        isclinic: '1'
      }
      setTimeout(() => {
        this.bmaplist()
      }, 600)
      this.$refs[formName].resetFields()
    },
    addsale (formName) {
      this.dialogVisiblesale = true
      this.show_sale = false
      this.addTableDatasale = {
        fathername: '',
        fatherphone: '',
        agent_products: [],
        agent_region: [],
        phone: '',
        level: '',
        sex: 1,
        address: '',
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      }
      this.saleList()
      this.$refs[formName].resetFields()
    },
    addagent () {
      this.dialogVisibleagent = true
      this.agentid = ''
      this.radio2 = 1
      this.addTableDataagent = {
        point: '',
        radio2: 1,
        agent_region: [],
        agent_products: [],
        phone: '',
        level: '',
        sex: 1,
        status: 1,
        label: '',
        aid: '-1',
        id: ''
      }
      if(!this.agentid){
        this.agentList()
        this.goodshow = true
        this.daagent = false
      }else{
        this.goodshow = false
        this.daagent = true
      }
    },
    editor (index, id, list, formName) {
      this.dialogVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    editordrug (index, id, list, formName) {
      this.dialogVisibledrug = true
      this.addTableDatadrug = Object.assign({}, list[index])
      this.imageUrl = list[index].shop_image
      this.imagelicence = list[index].licence_imgurl
      setTimeout(() => {
        var _this = this
        var longitude = this.addTableDatadrug.longitude
        var latitude = this.addTableDatadrug.latitude
        var map = new BMap.Map('container')
        map.setDefaultCursor('crosshair')
        map.enableScrollWheelZoom()
        var point = new BMap.Point(longitude, latitude)
        map.centerAndZoom(point, 13)
        var gc = new BMap.Geocoder()
        map.addControl(new BMap.NavigationControl())
        map.addControl(new BMap.OverviewMapControl())
        map.addControl(new BMap.ScaleControl())
        map.addControl(new BMap.MapTypeControl())
        map.addControl(new BMap.CopyrightControl())
        var marker = new BMap.Marker(point)
        map.addOverlay(marker)
        marker.enableDragging()
        function showLocationInfo (pt, rs) {
          var opts = {
            title: '当前位置'
          }
          var addComp = rs.addressComponents
          var addr =
            addComp.province +
            addComp.city +
            addComp.district +
            addComp.street +
            addComp.streetNumber
          var infoWindow = new BMap.InfoWindow(addr, opts)
          marker.openInfoWindow(infoWindow)
          _this.addTableDatadrug.address =
            addComp.province +
            addComp.city +
            addComp.district +
            addComp.street +
            addComp.streetNumber
        }
        map.addEventListener('click', function (e) {
          var marker = new BMap.Marker(e.point)
          _this.addTableDatadrug.longitude = e.point.lng
          _this.addTableDatadrug.latitude = e.point.lat
          gc.getLocation(e.point, function (rs) {
            showLocationInfo(e.point, rs)
          })
          map.clearOverlays(marker)
          map.addOverlay(marker)
        })
        var traffic = new BMap.TrafficLayer()
        map.addTileLayer(traffic)
      }, 600)
      this.defaultMsg = list[index].be_good_at || ''
      this.sureLoading = false
      this.areacontent = true
      this.$refs[formName].resetFields()
    },
    editorsale (index, id, list, formName) {
      this.dialogVisiblesale = true
      this.show_sale = true
      this.sureLoading = false
      this.addTableDatasale = Object.assign({}, list[index])
      this.province = list[index].region_province
      this.city = list[index].region_city
      this.district = list[index].region_district
      this.saleList()
      this.$refs[formName].resetFields()
    },
    editoragent (index, id, list) {
      this.dialogVisibleagent = true
      this.clickagen = ''
      this.sureLoading = false
      this.addTableDataagent = Object.assign({}, list[index])
      if (!list[index].fatherphone) {
        this.tableDatagoods = []
        this.addTableDataagent.agent_products = ''
      } else {
        this.querySearch()
      }
      this.province = list[index].region_province
      this.city = list[index].region_city
      this.district = list[index].region_district
      if(list[index].fatherid === -2){
        this.goodshow = true
        this.daagent = false
        this.agentList()
      }else{
        this.goodshow = false
        this.daagent = true
      }   
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    handleCurrentChange1 (currentPage1) {
      this.currentPage1 = currentPage1
      this.drugseach()
    },
    handleSizeChange2 (size) {
      this.pagesize2 = size
    },
    handleCurrentChange2 (currentPage2) {
      this.currentPage2 = currentPage2
      this.agentList()
    },
    // 查看病历
    handleCurrentChangebook (currentPagebook) {
      this.currentPagebook = currentPagebook
      this.seecase()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': this.formInline.type,
          'name': this.formInline.name,
          'phone': this.formInline.phone
        }
      }
      if (this.formInline.type === 1) {
        this.patient = true
      } else {
        this.patient = false
      }
      if (this.formInline.type === 2) {
        this.drugshop = true
      } else {
        this.drugshop = false
      }
      if (this.formInline.type === 3 || this.formInline.type === 4) {
        this.saleagent = true
      } else {
        this.saleagent = false
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData = []
          this.total = 0
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 点击新增代理商弹窗的内容
    agentList (isSearch) {
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline2.name,
          'sn': this.formInline2.sn,
          'enddate': this.formInline2.enddate
        }
      }
      var pa = this.currentPage2
      this.$axios.post(domain.testUrl + '/admin/product/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatagood = da.data.list
          this.total2 = da.data.total
          this.loading = false
          this.tableDatagood.map((v, i) => {
            this.$set(v, 'num2', 0)
            v.final = v.default + v.num2
          })
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
          this.total2 = 0
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 搜索药品
    drugseach (isSearch) {
      if (isSearch) {
        this.currentPage1 = 1
      }
      var pa = this.currentPage1
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          name: this.formInline.name
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData3 = da.data.list
          this.tableData3.map((v, i) => {
          // 必须要这样赋值
            this.$set(v, 'num1', 0)
            v.final = v.default + v.num1
          })
          this.totalpe = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData3 = []
          this.totalpe = 0
          this.$Notice.warning({title: da.message})
        }
      })
    },
    addcase () {
      this.addTableDatadrug.remarks = ''
      this.addTableDatacase = {
        age: '',
        sex: 1,
        num1: ''
      }
      let _this = this
      this.shopname = ''
      var pa = this.currentPage
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          'type': '2'
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDataShopname = da.data.list
        } else {
          this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 销售点击新增弹窗里面的内容
    saleList (isSearch) {
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline.name,
          'sn': this.formInline.sn,
          'enddate': this.formInline.enddate,
          'stock': this.formInline.stock
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatagood = da.data.list
          this.total2 = da.data.total
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
          this.total2 = 0
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 详情
    detail (id) {
      if (this.formInline.type === 1) {
        this.patientdetial = true
        this.allname = false
        this.sg = false
      } else {
        this.patientdetial = false
      }
      if (this.formInline.type === 2) {
        this.drug = true
        this.allname = true
        this.sg = false
      } else {
        this.drug = false
      }
      if (this.formInline.type === 3) {
        this.saledetail = true
        this.allname = true
        this.sg = true
      } else {
        this.saledetail = false
      }
      if (this.formInline.type === 4) {
        this.agentdetail = true
        this.allname = true
        this.sg = true
      } else {
        this.agentdetail = false
      }
      let d = {
        tag: 'member',
        op: 'getone',
        condition: {
          'id': id
        }
      }
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data
          this.agenname = da.data.father.uname
          this.agenphone = da.data.father.phone
        } else {
          this.loading = false
        }
      })
    },
    // 设置角色
    setadddrug (id) {
      let _this = this
      let d = {
        tag: 'member',
        op: 'role',
        condition: {
          id: id
        },
        data: {
          type: '2'
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    setaddsale (id) {
      let _this = this
      let d = {
        tag: 'member',
        op: 'role',
        condition: {
          id: id
        },
        data: {
          type: '3'
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    setaddagent (id) {
      let _this = this
      let d = {
        tag: 'member',
        op: 'role',
        condition: {
          id: id
        },
        data: {
          type: '4'
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 0
      } else {
        status = 1
      }
      let d = {
        tag: 'member',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
