<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>消息</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="email_content">
          <!-- <el-form :model="addTableData" label-width="150px" class="demo-ruleForm"> -->
        <form :model="addTableData">
          <h2 class="prize_count">收件人</h2>
          <div class="fold">
            <el-button type="primary" @click="Patient(type)">病人</el-button>
            <el-button type="success" @click="clinic(type)">诊所</el-button>
            <el-button type="info" @click="Pharmacy(type)">药店</el-button>
            <el-button type="warning" @click="sale(type)">销售</el-button>
            <el-button type="danger" @click="agent(type)">代理商</el-button>
          </div>
          <h2 class="prize_count">发送的邮件内容</h2>
          <textarea rows="10" cols="70"
            placeholder="请输入内容"
            class="email_text"
            v-model="addTableData.msg">
            </textarea>
        </form>
        <div slot="footer" class="dialog-footer tc">
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'>发 送</el-button>
        </div>
        <!-- 弹窗 -->
        <el-dialog
          :title="'选择病人'" v-model="addTableData.id"
          :visible.sync="dialogVisible"
          width="40%">
          <el-form :model="addTableData" ref="ruleForm" class="demo-ruleForm">
              <el-form-item>
                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                <div style="margin: 15px 0;"></div>
                <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                  <el-checkbox v-for="city in cities" :label="city" :key="city.id">{{city.name}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="cancelForm('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="patsubmitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
          </span>
        </el-dialog>
        <!-- 诊所、药店 -->
        <el-dialog
          :title="'选择病人'" v-model="addTableData.id"
          :visible.sync="drugdialogVisible"
          width="40%">
          <el-form :model="addTableData" ref="ruleForm" class="demo-ruleForm">
              <el-form-item>
                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                <div style="margin: 15px 0;"></div>
                <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                  <el-checkbox v-for="item in cities" :label="item" :key="item.id">{{item.shop_name}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="cancelForm('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="patsubmitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
          </span>
        </el-dialog>
        <!-- 销售代理商 -->
        <el-dialog
          :title="'选择病人'" v-model="addTableData.id"
          :visible.sync="saledialogVisible"
          width="40%">
          <el-form :model="addTableData" ref="ruleForm" class="demo-ruleForm">
              <el-form-item>
                <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                <div style="margin: 15px 0;"></div>
                <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                  <el-checkbox v-for="item in cities" :label="item" :key="item.id">{{item.uname}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="cancelForm('ruleForm')">取 消</el-button>
            <el-button type="primary" @click="patsubmitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      activeName: '',
      selectData: [],
      tableData: [],
      dialogVisible: false,
      saledialogVisible: false,
      drugdialogVisible: false,
      imageUrl: '',
      state: 1,
      sureLoading: false,
      addTableData: {
        name: []
      },
      type: '',
      isclinic: '',
      checkAll: false,
      checkedCities: [],
      cities: [],
      isIndeterminate: true,
      arrd: []
    }
  },
  activated () {
    // this.getList()
  },
  methods: {
    Patient (type) {
      this.arrd = []
      this.type = '1'
      this.dialogVisible = true
      this.Patientlist()
    },
    clinic (type) {
      this.arrd = []
      this.type = '2'
      this.isclinic = '2'
      this.drugdialogVisible = true
      this.Patientlist()
    },
    Pharmacy (type) {
      this.arrd = []
      this.type = '2'
      this.isclinic = '1'
      this.drugdialogVisible = true
      this.Patientlist()
    },
    sale (type) {
      this.arrd = []
      this.type = '3'
      this.saledialogVisible = true
      this.Patientlist()
    },
    agent (type) {
      this.arrd = []
      this.type = '4'
      this.saledialogVisible = true
      this.Patientlist()
    },
    // 全选
    handleCheckAllChange (val) {
      this.checkedCities = val ? this.cities : []
      this.isIndeterminate = false
      this.restaurants = this.cities.map(user => {
        let {id} = user
        return {id}
      })
    },
    handleCheckedCitiesChange (value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.cities.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
      this.restaurants = value.map(user => {
        let {id} = user
        return {id}
      })
    },
    cancelForm () {
      this.dialogVisible = false
      this.saledialogVisible = false
      this.drugdialogVisible = false
    },
    add () {
      this.isIndeterminate = false
      this.isclinic = ''
      this.type = ''
      this.addManagerVisible = true
      this.addTableData = {
        name: ''
      }
    },
    // 获取病人的名称
    Patientlist () {
      let _this = this
      let da = {
        type: this.type,
        isclinic: this.isclinic
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/getmember', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.cities = da.data.list
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
    },
    // 确认新增病人的名字
    patsubmitForm () {
      this.dialogVisible = false
      this.saledialogVisible = false
      this.drugdialogVisible = false
      for (let index = 0; index < this.restaurants.length; index++) {
        // let res = this.restaurants[index].id
        this.arrd.push(this.restaurants[index].id)
      }
    },
    getList () {
      let d = {
        tag: 'banner_config',
        op: 'get'
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    submitForm () {
      let _this = this
      this.sureLoading = true
      let da = {
        tag: 'message',
        op: 'sendmsg',
        data: {
          ids: this.arrd,
          msg: this.addTableData.msg
        }
      }
      da.data.ids = da.data.ids.join()
      this.$axios.post(domain.testUrl + '/admin/message/index', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.sureLoading = false
          this.dialogVisible = false
          this.saledialogVisible = false
          this.drugdialogVisible = false
          this.addTableData.msg = ''
          _this.$Notice.success({title: da.message})
        } else {
          this.sureLoading = false
          _this.$Notice.warning({title: da.message})
        }
      })
    }
  }
}
</script>
<style>
.drug_check{
  margin: 0 30px;
}
</style>



