<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>操作日志</el-breadcrumb-item>
      </el-breadcrumb>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%;margin-top:10px">
          <el-table-column
          align='center'
          prop="account"
          label="操作人">
          </el-table-column>
          <el-table-column
          label="操作内容"
          prop="op"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          prop="time"
          label="操作时间">
          </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'list',
  data () {
    return {
      loading: null,
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      tableData: [],
      total: 0,
      dialogVisible: false
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {}
      this.$axios.post(domain.testUrl + '/admin/Systemsets/systemlog?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
</style>



