<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>订单列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative" :model="formInline">
            <div class="from-group" style="width:150px">
              <el-input
                v-model="formInline.order_id"
                placeholder="请输入订单号">
              </el-input>
            </div>
            <div class="from-group form_phone">
              <el-input
                v-model="formInline.phone"
                placeholder="请输入电话号码">
              </el-input>
            </div>
            <div class="from-group" style="width:110px">
              <el-select v-model="formInline.status" placeholder="订单状态">
                  <el-option label="所有" :value="8"></el-option>
                  <el-option label="受理" :value="2"></el-option>
                  <el-option label="付款" :value="3"></el-option>
                  <el-option label="驳回" :value="4"></el-option>
                  <el-option label="挂单" :value="5"></el-option>
                  <el-option label="发货" :value="6"></el-option>
                  <el-option label="收货" :value="7"></el-option>
              </el-select>
            </div>
            <div class="from-group" style="width:150px">
              <el-date-picker
              width="80"
              v-model="formInline.create_date"
              default-time="00:00:00"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="getList(true)">查询</el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
          width="150"
          label="订单号"
          align='center'>
          <template slot-scope="scope">
            <p class="dtail_number" @click='detail(scope.row.order_id)'>{{scope.row.order_id}}</p>
          </template>
          </el-table-column>
          <el-table-column
          label="电话号码"
          prop="phone"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          prop="total_fee"
          label="订单总价格/元">
          </el-table-column>
          <el-table-column
          align='center'
          prop="create_date"
          label="创建时间">
          </el-table-column>
          <el-table-column
            align='center'
            label="发票">
            <template slot-scope="scope">
              <span v-if="!!scope.row.invoiceurl">有</span>
              <span v-else>无</span>
              <!-- <img v-if="!!scope.row.invoiceurl" :src="scope.row.invoiceurl" width="150" height="100">
              <template v-else>无</template> -->
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          label="订单状态">
            <template slot-scope="scope">
                <p v-if="scope.row.status == 0">已删除</p>
                <p v-else-if="scope.row.status == 1">待受理</p>
                <p v-else-if="scope.row.status == 2">已受理</p>
                <p class="enable" v-else-if="scope.row.status == 3">已付款</p>
                <p class="disable" v-else-if="scope.row.status == 4">已驳回</p>
                <p class="orange" v-else-if="scope.row.status == 5">已挂单</p>
                <p class="disable" v-else-if="scope.row.status == 6">已发货</p>
                <p class="disable" v-else-if="scope.row.status == 7">已收货</p>
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="220"
          label="操作">
          <template slot-scope="scope">
            <el-dropdown @command="handleCommand(scope.row.order_id,$event,scope.row.type)" class="el_more">
                <span class="el-dropdown-link">
                  更多<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                  <div v-if="scope.row.status === 1">
                    <el-dropdown-item command="a">受理</el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 2">
                    <el-dropdown-item command="c">付款</el-dropdown-item>
                    <el-dropdown-item command="d">驳回</el-dropdown-item>
                    <el-dropdown-item command="e">挂单</el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 3">
                    <el-dropdown-item command="f">发货</el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 4">
                    <el-dropdown-item command="h">暂无</el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 5">
                    <el-dropdown-item command="f">发货</el-dropdown-item>
                  </div>
                  <div v-if="scope.row.status === 6">
                    <el-dropdown-item command="g">收货</el-dropdown-item>
                  </div>
                </el-dropdown-menu>
              </el-dropdown>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.order_id)"><span>删除</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        </el-table>
        <!-- 新增 -->
        <!-- :close-on-click-modal="false"
        :close-on-press-escape="false" -->
        <el-dialog
        :title="addTableData.aid==='-1'?'新增订单':'修改订单'" v-model="addTableData.id"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form :model="addTableData" label-width="100px" class="demo-ruleForm">
          <el-form-item label="上传电子发票">
            <el-input v-model="addTableData.invoiceurl " style="width:70%" placeholder="上传电子发票" readonly="readonly"></el-input>
            <el-upload class="upload-demo fr"
                action="123"
                :before-upload="beforeUpload"
                ref="newupload"
                :auto-upload="true">
                <el-button slot="trigger" size="small" type="primary"  @click="newSubmitForm()">选取文件</el-button>
              </el-upload>
          </el-form-item>
          <el-form-item label="收货人电话" maxlength="11">
            <el-input v-model="addTableData.phone" placeholder="请输入电话号码" style="width:160px" maxlength="11"></el-input>
            <el-button size="small" class="surephone" @click="surenumber">确认</el-button>
          </el-form-item>
          <!-- 收货人 -->
          <el-table
          v-show="numberphone"
          :data="tableDataone"
          style="width: 100%">
          <el-table-column
            label="昵称">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            label="电话号码"
            prop="phone">
          </el-table-column>
          <el-table-column
            label="地址"
            prop="address">
            <!-- <template slot-scope="scope">
                <Button type="info">确认</Button>
            </template> -->
          </el-table-column>
        </el-table>
        <el-form-item v-show="numberphone" label="发货人电话" maxlength="11" class="busines">
          <el-input v-model="addTableData.namephone" placeholder="请输入电话号码" style="width:160px" maxlength="11"></el-input>
          <el-button size="small" class="surephone" @click="goodsumber">确认</el-button>
        </el-form-item>
        <!-- 发货人信息 -->
          <el-table
          v-show="goodshow"
          :data="tableDatatwo"
          style="width: 100%">
          <el-table-column
            label="昵称">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            label="电话号码"
            prop="phone">
          </el-table-column>
          <el-table-column
            label="地址"
            prop="address">
          </el-table-column>
        </el-table>
        <!-- 代理的药品列表 -->
          <form class="from-reset relative f_seach d_seach" :model="formInline" v-show="goodshow">
            <div class="from-group form_dugr">
              <el-input
                v-model="formInline.name"
                placeholder="请输入药品名">
              </el-input>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="drugseach(true)">查询</el-button>
            </div>
          </form>
          <el-table
          v-show="goodshow"
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column
            prop="name"
            label="药品名称">
          </el-table-column>
          <el-table-column
            prop="price"
            label="单价"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            prop="num"
            label="库存"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="总金额"
            show-overflow-tooltip>
            <template slot-scope="scope">
              <span class="red">{{scope.row.totalmoney}}</span>
            </template>
          </el-table-column>
          <el-table-column
            width="180"
            label="数量">
            <template slot-scope="scope">
              <el-input-number v-model="scope.row.num1" @change="handlenumber(scope.row, $event)" :min="0"></el-input-number>
            </template>
          </el-table-column>
        </el-table>
        <div class="detail_page" v-show="goodshow">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange1"
              :current-page="currentPage1"
              :page-sizes="pageSizes"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="totalpe">
          </el-pagination>
        </div>
        <el-form-item label="总金额：" v-show="goodshow"><span class="totles">{{totals}}/元</span></el-input></el-form-item>
        <el-form-item label="折扣：" v-show="goodshow">
            <el-input v-model="addTableData.discount" placeholder="请输入折扣金额" style="width:160px" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="实收金额：" v-show="goodshow">
          <el-input v-model="addTableData.receipts" placeholder="请输入实收金额" style="width:160px" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="备注：" v-show="goodshow">
          <el-input
            type="textarea"
            autosize
            placeholder="请输入备注"
            v-model="addTableData.remarks">
          </el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-cons">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
      <!-- 已付款备注 -->
        <el-dialog
        :title="'操作备注'"
        :visible.sync="dialogpayment"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        width="35%">
        <el-form :model="paymentTableData" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="备注">
            <el-input
              type="textarea"
              autosize
              placeholder="请输入此次操作的备注"
              v-model="paymentTableData.remark">
            </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitpayment('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
<!-- 订单详情 -->
        <el-dialog
        :title="'订单详情'"
        :visible.sync="dialogdetail"
        width="45%">
        <el-table
        v-loading="loading"
        :data="tableDatadetail"
          border
          style="width: 100%;margin-top:15px">
          <el-table-column
          align='center'
          prop="company"
          label="药店">
          </el-table-column>
          <el-table-column
          align='center'
          prop="name"
          label="药品">
          </el-table-column>
          <el-table-column
          align='center'
          prop="num"
          label="数量">
          </el-table-column>
          <el-table-column
          align='center'
          prop="trueprice"
          label="单价/元">
          </el-table-column>
          <el-table-column
          align='center'
          prop="totalprice"
          label="总价/元">
          </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange2"
            @current-change="handleCurrentChange2"
            :current-page="currentPage2"
            :page-sizes="pageSizes2"
            :page-size="pagesize2"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totaldetail">
        </el-pagination>
        <div class="bre">
          <h3>备注:</h3>
          <p class="detail_remark">{{remarkss}}</p>
        </div>
        <div>
          <h3>合计支付:<span class="totles">{{totalfee}}/元</span></h3>
        </div>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      currentPage1: 1,
      currentPage2: 1,
      pageSizes: [10],
      pagesize: 10,
      pageSizes2: [10],
      pagesize2: 10,
      tableData: [],
      tableDatadetail: [],
      tableDatatwo: [],
      totalpe: 0,
      total: 0,
      totaldetail: 0,
      loading: true,
      sureLoading: false,
      numberphone: false,
      shoplist: false,
      goodshow: false,
      classA: false,
      classB: true,
      aid: '',
      imageUrl: '',
      remarkss: '',
      totalfee: '',
      // 搜索
      formInline: {
        order_id: '',
        phone: '',
        status: '',
        create_date: ''
      },
      dialogVisible: false,
      dialogdetail: false,
      dialogpayment: false,
      addTableData: {
        namephone: '',
        phone: '',
        name: '',
        aid: '',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        discount: '',
        receipts: '',
        tag: '',
        invoiceurl: '',
        totals: '0',
      },
      paymentTableData: {
        remark: ''
      },
      tableDataone: [],
      tableData3: [],
      num: 1,
      pp: '',
      va: '',
      totalmoney: '',
      arrayFilter: [],
      totals: '0'
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    beforeUpload (file) {
      let da = new FormData()
      da.append('image', file)
      da.append('dic', 'invoice')
      this.$axios.post(domain.testUrl + '/admin/upload/index', da, {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.addTableData.invoiceurl = da.data
          this.$Notice.success({title: da.message})
        } else {
          this.loading = false
          this.$Notice.warning({title: da.message})
        }
      })
      return true
    },
    detail (id) {
      let d = {
        tag: 'order',
        op: 'getone',
        condition: {
          'id': id
        },
        data: {
          status: 1
        }
      }
      var pa = this.currentPage2
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/order/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data.goodsinfo.list
          this.totaldetail = da.data.goodsinfo.total
          this.remarkss = da.data.remarkss
          this.totalfee = da.data.total_fee
        } else {
          this.loading = false
        }
      })
    },
    handleCommand (id, command, type) {
      let _this = this
      this.pointid = id
      _this.dialogpayment = true
      if (command === 'a') {
        this.state = '2'
      } else if (command === 'c') {
        this.state = '3'
      } else if (command === 'd') {
        this.state = '4'
      } else if (command === 'e') {
        this.state = '5'
      } else if (command === 'f') {
        this.state = '6'
      } else if (command === 'g') {
        this.state = '7'
      }
    },
    // 点击新增数量
    handlenumber (t, value) {
      var totalPrice = 0
      t.totalmoney = Math.floor((t.price * value) * 100) / 100
      var arry = this.tableData3.map(user => {
        let {id, num1, price,totalmoney} = user
        let num = user.num1
        return {id, num, price,totalmoney}
      })
      this.arrayFilter = arry.filter(item => {
        return item.num !== 0
      })
      this.arrayFilter.forEach(function (val,index) {
        totalPrice += val.num*val.price;//累计总价
      });
      this.totals =parseFloat(totalPrice);
    },

    // 收货人信息
    surenumber () {
      if (this.addTableData.phone === '' || this.addTableData.phone === null || this.addTableData.phone === undefined) {
        this.$Notice.warning({title: '电话号码不能为空'})
        return
      }
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          phone: this.addTableData.phone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDataone = da.data.list
          this.numberphone = true
          this.recoverid = da.data.list[0].id
        } else {
          this.numberphone = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    drugseach (isSearch) {
      if (isSearch) {
        this.currentPage = 1
      }
      let d = {
        tag: 'member',
        op: 'getmemberproduct',
        condition: {
          id: this.listid
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData3 = da.data.list
          this.tableData3.map((v, i) => {
            this.$set(v, 'num1', 0)
            this.$set(v, 'totalmoney', 0)
            v.final = v.default + v.num1
          })
          this.totalpe = da.data.total
          this.loading = false
        } else {
          this.loading = false
          this.tableData3 = []
          this.totalpe = 0
          this.$Notice.warning({title: da.message})
        }
      })
    },
    // 发货人信息
    goodsumber () {
      if (this.addTableData.namephone === '' || this.addTableData.namephone === null || this.addTableData.namephone === undefined) {
        this.$Notice.warning({title: '电话号码不能为空'})
        return
      }
      let d = {
        tag: 'member',
        op: 'getlist',
        condition: {
          phone: this.addTableData.namephone
        }
      }
      this.$axios.post(domain.testUrl + '/admin/member/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatatwo = da.data.list
          this.listid = da.data.list[0].id
          this.goodshow = true
          this.drugseach()
        } else {
          this.goodshow = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    add (formName) {
      this.imageUrl = ''
      this.dialogVisible = true
      this.goodshow = false
      this.numberphone = false
      this.addTableData = {
        name: '',
        aid: '-1',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        discount: '',
        receipts: '',
        tag: '',
        num: 1,
        invoiceurl: '',
        totals: '0',
        remarks: '',
        discount: '',
        receipts: ''
      }
      this.tableDatatwo = []
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    handleCurrentChange1 (currentPage1) {
      this.currentPage1 = currentPage1
      this.getList()
    },
    handleSizeChange2 (size) {
      this.pagesize2 = size
    },
    handleCurrentChange2 (currentPage2) {
      this.currentPage2 = currentPage2
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'order',
        op: 'getlist',
        condition: {
          'order_id': this.formInline.order_id,
          'phone': this.formInline.phone,
          'status': '',
          'create_date': this.formInline.create_date
        }
      }
      if (this.formInline.status === 5) {
        d.condition.status = ''
      } else {
        d.condition.status = this.formInline.status
      }
      this.$axios.post(domain.testUrl + '/admin/order/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.tableData = []
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    cancelForm (formName) {
      this.dialogVisible = false
      this.dialogpayment = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      this.sureLoading = true
      let da = {
        tag: 'order',
        op: 'add',
        data: {
          member_id: _this.recoverid,
          to_id: _this.listid,
          invoiceurl: domain.testUrl + _this.addTableData.invoiceurl,
          remarks: _this.addTableData.remarks,
          discount: _this.addTableData.discount,
          receipts: _this.addTableData.receipts,
          goods: _this.arrayFilter
        }
      }
      this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.getList()
          this.dialogVisible = false
          this.sureLoading = false
          _this.$Notice.success({title: da.message})
        } else {
          this.sureLoading = false
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    submitpayment () {
      let _this = this
      let d = {
        tag: 'order',
        op: 'state',
        condition: {
          id: this.pointid
        },
        data: {
          status: this.state,
          remark: this.paymentTableData.remark
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          this.dialogpayment = false
          _this.$Notice.success({title: da.message})
        } else {
          _this.$Notice.warning({title: da.message})
        }
      })
    },
    // 删除
    deleted (id) {
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'order',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0',
            remark: ''
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$Notice.success({title: da.message})
          } else {
            _this.$Notice.warning({title: da.message})
          }
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'product',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>



