<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix card-header">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>微信基本设置</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="set_content">
          <el-form :model="addTableData" label-width="120px" class="demo-ruleForm">
            <el-form-item label="名字">
              <el-input v-model="addTableData.title" placeholder="请输入用户名"></el-input>
            </el-form-item>
            <el-form-item label="密码：">
              <el-input v-model="addTableData.url" placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-form-item label="短信有效期/月：">
              <el-input v-model="addTableData.url" placeholder="请输入短信有效期"></el-input>
            </el-form-item>
          </el-form>
        <div slot="footer" class="dialog-footer tc">
          <el-button type="primary" @click="submitForm()" :loading='sureLoading'>提 交</el-button>
        </div>
      </div>
      </div>
    </el-card>
  </div>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      selectData: [],
      tableData: [],
      addManagerVisible: false,
      imageUrl: '',
      state: 1,
      sureLoading: false,
      addTableData: {
        url: '',
        state: 1,
        imageUrl: 'adsfjklasfjklsajfk',
        aid: ''
      }
    }
  },
  methods: {
    submitForm () {
      let _this = this
      this.sureLoading = true
      let da = {
        tag: 'banner_config',
        op: 'add',
        data: {
          img: 'sss.img',
          url: this.addTableData.url,
          title: this.addTableData.title,
          state: this.addTableData.state
        }
      }
      this.$axios.post(domain.testUrl + '/admin/systemsets/banner_or_ad_set', qs.stringify(da), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.getList()
          this.sureLoading = false
          this.addManagerVisible = false
          _this.$Notice.success({title: da.message})
        } else {
          this.sureLoading = false
          _this.$Notice.warning({title: da.message})
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



