<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>文章咨询</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            class="picture_drug"
            label="图片">
            <template slot-scope="scope">
              <img v-if="!!scope.row.imgurl" :src="scope.row.imgurl" width="100" height="80">
              <template v-else>无</template>
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          prop="title"
          label="标题">
          </el-table-column>
          </el-table-column>
          <el-table-column
          label="发表日期"
          prop="create_time"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          label="状态">
          <template slot-scope='scope'>
            <span
            @click="changeState(scope.row.id,scope.row.status)"
            :class="scope.row.status == '1'?'tag_success':'tag_danger'"
            >{{scope.row.status == "1"?'启用':'禁用'}}
            </span>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="180"
          label="操作">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editorlist(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.id)"><span>删除</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <el-dialog
        :title="addTableData.aid==='-1'?'新增文章':'修改文章'" v-model="addTableData.id"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form :model="addTableData" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="标题">
            <el-input v-model="addTableData.title" placeholder="请输入文章标题"></el-input>
          </el-form-item>
          <el-form-item label="封面图片">
              <el-upload
                id="el-upload1"
                class="avatar-uploader avatar-uploader_356x178 avatar-uploader_contain"
                action="11111"
                :show-file-list="false"
                :on-change='formImageChange'
                :auto-upload='false'
                :on-success="handleAvatarSuccess">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          <div class="detail">文章内容</div>
          <UE v-if='dialogVisible' class="ueeclsjdf" :defaultMsg='defaultMsg' :config='config' ref="ue"></UE>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
import UE from '../../components/ue/ue.vue'
export default {
  name: 'list',
  components: {UE},
  data () {
    return {
      editor: '',
      defaultMsg: '',
      defaultMsgName: '',
      abb: '',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      isshow: true,
      loading: null,
      // 分页数据
      currentPage: 1,
      pageSizes: [10],
      pagesize: 10,
      tableData: [],
      total: 0,
      sureLoading: false,
      aid: '',
      dialogVisible: false,
      dialogTableVisible: false,
      imageUrl: '',
      addTableData: {
        title: '',
        aid: '',
        text: '',
        imageUrl: '',
        status: 1
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    formImageChange (file, fileList) {
      this.forid = 1
      this.imageUrl = URL.createObjectURL(file.raw)
      let d = new FormData()
      d.append('image', file.raw)
      d.append('dic', 'other')
      this.$axios.post(domain.testUrl + '/admin/upload/index', d, {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.addTableData.imgurl = da.data
          this.$Notice.success({title: da.message})
        } else {
          this.loading = false
          this.$Notice.warning({title: da.message})
        }
      })
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },
    cancelForm (formName) {
      this.dialogVisible = false
    },
    editorlist (index, id, list, formName) {
      this.dialogVisible = true
      this.addTableData = Object.assign({}, list[index])
      this.defaultMsg = list[index].text || ''
      this.imageUrl = list[index].imgurl
      this.sureLoading = false
    },
    submitForm (formName) {
      let _this = this
      this.sureLoading = true
      if (_this.addTableData.aid === '-1') {
        let da = {
          tag: 'news',
          op: 'add',
          data: {
            title: this.addTableData.title,
            status: this.addTableData.status,
            imgurl: this.addTableData.imgurl,
            text: this.$refs.ue.getUEContent()
          }
        }
        this.$axios.post(domain.testUrl + '/admin/news/index', qs.stringify(da), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            this.getList()
            this.dialogVisible = false
            this.sureLoading = false
            _this.$Notice.success({title: da.message})
          } else {
            this.sureLoading = false
            _this.$Notice.warning({title: da.message})
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      } else {
        let da = {
          tag: 'news',
          op: 'update',
          condition: {
            id: this.addTableData.id
          },
          data: {
            title: this.addTableData.title,
            status: this.addTableData.status,
            imgurl: this.addTableData.imgurl,
            text: this.$refs.ue.getUEContent()
          }
        }
        this.$axios.post(domain.testUrl + '/admin/news/index', qs.stringify(da), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            this.getList()
            this.dialogVisible = false
            this.sureLoading = false
            _this.$Notice.success({title: da.message})
          } else {
            this.sureLoading = false
            _this.$Notice.warning({title: da.message})
          }
        })
      }
    },
    add (formName) {
      this.imageUrl = ''
      this.dialogVisible = true
      this.addTableData = {
        aid: '-1',
        title: '',
        text: '',
        imageUrl: '',
        status: 1
      }
      this.defaultMsg = ''
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      var pa = this.currentPage
      let d = {
        tag: 'news',
        op: 'getlist',
        condition: {
          'status': ''
        }
      }
      this.$axios.post(domain.testUrl + '/admin/news/index?' + 'limit=10&page=' + pa, qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    // 删除
    deleted (id) {
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'news',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0'
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/news/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
          } else {
            _this.$notify({
              title: '提示',
              message: da.data.message,
              type: 'warning'
            })
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'news',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/news/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



